import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs';
import { ThreadCategory, Collections } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';

@IonicPage()
@Component({
  selector: 'page-foros',
  templateUrl: 'foros.html',
})
export class ForosPage {

  public cats$: Observable<ThreadCategory[]> = this.afs.collection<ThreadCategory>(Collections.THREAD_CATEGORY).valueChanges()

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore
  ) {
  }

  ionViewDidLoad() {
    
  }

}



